.paginator {
    margin: 20px;
}

.pageNumber {
    padding: 7px;
    border: 2px solid green;
    font-weight: bold;
    color: white;
}

.selectedPage {
    padding: 7px;
    border: 1px solid black;
    color: #e6dede;
}

.nextButton {
    color: blue;
}

.prevButton {
    color: green;
}